<template>
  <section class="page_container">
    <Mark />
    <BigNav />
    <SmallNav />
    <Header />
    <IpadSection />
    <AllProjects @open-popup="toggleProject" />
    <AboutCard />
    <Skills />
    <Contact />
    <ProjectPopup @close-popup="toggleProject" :info="openProject" />
  </section>
</template>

<script>
import { ref } from "vue";
import BigNav from "./BigNav.vue";
import SmallNav from "./SmallNav.vue";
import Mark from "./Mark.vue";
import Header from "./Header.vue";
import IpadSection from "./IpadSection.vue";
import AllProjects from "./AllProjects.vue";
import Skills from "./Skills.vue";
import Contact from "./Contact.vue";
import ProjectPopup from "./ProjectPopup.vue";
import AboutCard from "./AboutCard.vue";
export default {
  components: {
    BigNav,
    Mark,
    Header,
    IpadSection,
    AllProjects,
    Skills,
    Contact,
    SmallNav,
    ProjectPopup,
    AboutCard,
  },
  setup() {
    document.title = "HAOKAI";
    const openProject = ref({
      show: false,
    });
    function toggleProject(info) {
      if (info.show === true) {
        openProject.value = info;
      } else {
        openProject.value = {
          show: false,
        };
      }
    }

    return {
      toggleProject,
      openProject,
    };
  },
};
</script>

<style scoped>
.page_container {
  max-width: 2000px;
  width: 100%;
  padding: 0 2em;
  margin: 0 auto;
  overflow-x: hidden;
  position: relative;
}

.big_nav {
  width: 100%;
  height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 6em;
}
.small_nav {
  display: none;
}
.ipadSection {
  width: 100%;
  height: 100%;
  margin: 15em 0;
}
.allProjects {
  width: 100%;
  padding: 2em;
  position: relative;
  margin: 2em 0 3em 0;
}
.about {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: var(--grey-400);
  padding: 10em 0;
}
.skills {
  width: 90%;
  margin: 3em auto;
  margin-bottom: 3em;
}
.contact {
  height: 15em;
}
@media screen and (max-width: 1000px) {
  .allProjects {
    width: 100%;
    padding: 0.5em;
  }
}
@media screen and (max-width: 900px) {
  .big_nav {
    display: none;
  }
  .small_nav {
    display: flex;
  }
}
@media screen and (max-width: 750px) {
  .ipadSection {
    width: 100%;
    height: 100%;
  }
  .skills {
    width: 90%;
  }
}
@media screen and (max-width: 500px) {
  .page_container {
    max-width: 2000px;
    width: 100%;
    padding: 0 0em;
    margin: 0 auto;
    overflow-x: hidden;
  }
  .skills {
    width: 90%;
  }
}
</style>
