<template>
  <div class="small_nav" :class="{ show: openNav }">
    <div class="toggle_btn" :class="{ show: openNav }" @click="toggleNav">
      <div class="toggle"></div>
    </div>
    <ul class="tags" :class="{ show: openNav }">
      <li @click="toggleNav">
        <a href="#home">Home</a>
      </li>
      <li @click="toggleNav">
        <a href="#allProjects">Projects</a>
      </li>
      <li @click="toggleNav">
        <a href="#about">About</a>
      </li>
      <li @click="toggleNav">
        <a href="#skill">Skills</a>
      </li>
      <li @click="toggleNav">
        <a href="#contact">Contact</a>
      </li>
    </ul>
  </div>
</template>

<script>
import { ref } from "vue";
export default {
  setup() {
    const openNav = ref(false);
    function toggleNav() {
      openNav.value = !openNav.value;
    }

    return {
      openNav,
      toggleNav,
    };
  },
};
</script>

<style scoped>
.small_nav {
  width: 5em;
  height: 5em;
  background-color: none;
  position: fixed;
  top: 1em;
  right: 1em;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
}

.small_nav.show {
  width: 100%;
  height: 100%;
  background-color: var(--primary-1);
  top: 0em;
  right: 0em;
  display: block;
  transform-origin: right;
  transition: all 0.5s;
  border-radius: 2px;
}

.toggle_btn {
  width: 3em;
  height: 3em;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s;
}
.toggle {
  width: 100%;
  background-color: var(--primary-1);
  height: 2px;
  position: relative;
  transition: all 0.5s;
}
.toggle::before,
.toggle::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--primary-1);
}
.toggle::before {
  position: absolute;
  top: 1em;
  left: 0;
}
.toggle::after {
  position: absolute;
  bottom: 1em;
  left: 0;
}

.toggle_btn.show {
  position: absolute;
  top: 1em;
  right: 1em;
}
.toggle_btn.show .toggle {
  transform: rotate(45deg);
  background-color: var(--grey-400);
}
.toggle_btn.show .toggle::before {
  background-color: var(--grey-400);

  transform: rotate(90deg);
  top: 0;
}
.toggle_btn.show .toggle::after {
  display: none;
}

.tags {
  width: 100%;
  max-width: 30em;
  position: absolute;
  top: 5em;
  left: 50%;
  transform: translateX(-50%);
  display: none;
}

li {
  width: 100%;
  font-size: 4rem;
  font-weight: 500;
}

li:not(:last-child) {
  margin-bottom: 1em;
}

li a {
  display: block;
  border-bottom: 2px solid var(--grey-400);
  color: var(--grey-400);
}
.tags.show {
  display: block;
}

@media screen and (max-width: 400px) {
  li {
    width: 90%;
    font-size: 3rem;
    margin: 0 auto;
  }
}
</style>
