<template>
  <div class="img_box">
    <div class="back" ref="ipadE2"></div>
    <img src="../assets/sna/orderHomeBipad.png" class="dashImg" ref="ipadEl" />
  </div>
</template>

<script>
import { useMotion } from "@vueuse/motion";
import { ref } from "vue";

export default {
  setup() {
    const ipadEl = ref(null);
    const ipadE2 = ref(null);

    useMotion(ipadE2, {
      initial: {
        opacity: 0,
        y: 100,
        rotateX: 90,
        rotateY: 45,
        rotateZ: 0,
      },
      visibleOnce: {
        opacity: 1,
        y: 0,
        rotateX: 45,
        rotateY: 45,
        rotateZ: -30,
        transition: {
          duration: 800,
        },
      },
      leave: {
        opacity: 0,
        y: -100,
      },
    });

    useMotion(ipadEl, {
      initial: {
        opacity: 0,
        y: 100,
        rotateX: 90,
        rotateY: 45,
        rotateZ: 0,
      },
      visibleOnce: {
        opacity: 1,
        y: 0,
        rotateX: 45,
        rotateY: 45,
        rotateZ: -30,
        transition: {
          duration: 800,
        },
      },
      leave: {
        opacity: 0,
        y: -10,
      },
    });
    return { ipadEl, ipadE2 };
  },
};
</script>

<style scoped>
.img_box {
  width: 50em;
  perspective: 1000px;
}
.back {
  width: 108%;
  height: 107%;
  background-color: rgb(69, 68, 68);
  position: absolute;
  top: -3.5%;
  left: -3.5%;
  border-radius: 10px;
  border: 0.7px solid #fff;
}

.dashImg {
  box-shadow: -10px 10px 20px rgba(0, 0, 0, 0.4);
  border-radius: 5px;
}

/* .dashImg,
.back {
  transform-style: preserve-3d;
  animation: routate 3s ease forwards;
} */

@keyframes routate {
  0% {
    transform: rotateX(90deg) rotateY(45deg) rotateZ(0deg);
  }
  30% {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(10deg);
  }
  100% {
    transform: rotateX(45deg) rotateY(45deg) rotateZ(-30deg);
  }
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (min-width: 1500px) {
  .img_box {
    width: 70em;
    perspective: 1000px;
  }
}
@media screen and (max-width: 750px) {
  .img_box {
    width: 70vw;
    min-width: 200px;
    perspective: 1000px;
  }
}
@media screen and (max-width: 430px) {
  .img_box {
    width: 80vw;
    min-width: 200px;
    perspective: 1000px;
  }
}
</style>
